import React from "react"
import Layout from "../component/layout"
import { graphql, Link } from "gatsby"
import { css } from "@emotion/core"
import Helmet from "react-helmet"
import apple_logo from "../image/apple_rainbow_logo.svg"
import mergeImageIcon from "../image/mergeImage.svg"
import watermarkIcon from "../image/watermark.svg"

const articlesContainerStyle = css`
  width: 90%;
  margin: 0rem auto 2rem auto;
  max-width: 600px;
  a {
    color: rgba(0, 0, 0, 0.8);
    text-decoration: none;
  }
  h2 {
    font-size: 1.2rem;
    letter-spacing: 0.5rem;
    margin: 0.5rem 0;
  }
  .article {
    margin-bottom: 0.7rem;
    a {
      color: rgba(0, 0, 0, 0.8);
      text-decoration: none;
    }
    a span:first-of-type {
      margin-right: 5px;
    }
    a span:last-of-type {
      font-size: 0.8rem;
      color: #757575;
    }
  }
`

const sectionTitleStyle = css`
  margin-bottom: 20px;
  font-weight: bold;
  background-color: #eee;
  text-align: center;
`

const toolsContainerStyle = css`
  width: 90%;
  margin: 0rem auto 2rem auto;
  max-width: 600px;
  a {
    color: rgba(0, 0, 0, 0.8);
    text-decoration: none;
  }
  .tools {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    grid-gap: 10px;
  }
  .tools .tool {
    width: 100%;
    height: 40px;
    background-color: #f5f7fa;
    box-shadow: 0 0 3px #aaa;
  }
  .tools .tool:hover {
    background-color: #c3cfe2;
  }
  .tools .tool a {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .tool-image img {
    width: 25px;
    height: 25px;
    vertical-align: middle;
    margin-right: 5px;
  }
  .tools .tool a .tool-title {
    font-size: 12px;
  }
`

const tools = [
  {
    title: '描述性文件',
    link: '/ios-beta-profile/',
    logo: apple_logo
  },
  {
    title: '更新记录',
    link: '/ios-version-history/',
    logo: apple_logo
  },
  {
    title: '功能可用性',
    link: '/apple-features-availability/',
    logo: apple_logo
  },
  {
    title: '图片拼接',
    link: '/merge-images/',
    logo: mergeImageIcon
  },
  {
    title: '图片加水印',
    link: '/add-watermark/',
    logo: watermarkIcon
  },
]

export default ({ data }) => {
  return (
    <Layout>
      <Helmet>
        <title>科科的个人博客</title>
        <meta name="description" content={data.site.siteMetadata.description} />
      </Helmet>
      <section css={toolsContainerStyle}>
        <div css={sectionTitleStyle}>工具</div>
        <div className="tools">
          {
            tools.map(tool => {
              return (
                <div className="tool">
                  <Link to={tool.link}>
                    <div className="tool-image">
                      <img src={tool.logo} alt="apple logo"></img>
                    </div>
                    <div className="tool-title">
                      {tool.title}
                    </div>
                  </Link>
                </div>
              )
            })
          }
        </div>
      </section>
      <section css={articlesContainerStyle}>
        <div css={sectionTitleStyle}>文章</div>
        {data.allMarkdownRemark.edges.map((edge, index) => {
          const slug = `/blog/${edge.node.frontmatter.slug}/`
          return (
            <div className="article" key={index}>
              <Link to={slug}>
                <span>{edge.node.frontmatter.title}</span>
                <span>{edge.node.frontmatter.date}</span>
              </Link>
            </div>
          )
        })}
      </section>
    </Layout>
  )
}

export const data = graphql`
  query {
    allMarkdownRemark(
      sort: { fields: frontmatter___date, order: DESC }
      filter: { frontmatter: { draft: { eq: false } } }
    ) {
      edges {
        node {
          frontmatter {
            title
            slug
            date(formatString: "YYYY/MM/DD")
          }
        }
      }
    }
    site {
      siteMetadata {
        description
      }
    }
  }
`
